.education {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column; }

.education__title {
  font-size: 1.2rem;
  font-weight: bold; }

.education__content__list {
  padding: 5px 0; }

.education__content__list__college {
  font-size: .9rem;
  text-transform: capitalize;
  font-weight: 600; }

.education__content__list__college__place {
  margin-left: 5px; }

.education__content__list__course {
  font-size: .8rem; }

.education__content__list__year {
  font-size: .8rem;
  font-style: italic; }
