.organization {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column; }

.organization__title {
  font-size: 1.2rem;
  font-weight: bold; }

.organization__content__list {
  padding: 5px 0; }

.organization__content__list__name {
  font-size: 1rem;
  font-weight: bold; }

.organization__content__list__role {
  font-size: .8rem; }

.organization__content__list__duration {
  font-size: .8rem;
  font-style: italic; }
