.skills {
  padding: 10px 0; }

.skills__title {
  font-size: 1.2rem;
  font-weight: bold; }

.skills__info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-align-items: center;
  align-items: center;
  padding: 5px 0; }

.skills__info__text {
  -webkit-flex: 0 0 30%;
  flex: 0 0 30%;
  font-size: .8rem;
  font-weight: bold; }

.skills__info__rating {
  -webkit-flex: 0 0 70%;
  flex: 0 0 70%; }

.skills__info .progress {
  height: 10px; }
