.about-me {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  padding: 15px 10px; }

.about-me__user-info {
  -webkit-flex: 0 0 35%;
  flex: 0 0 35%;
  text-transform: capitalize; }

.about-me__user-info__name {
  font-size: 1.5rem;
  font-weight: 600; }

.about-me__user-info__role {
  font-size: 1.1rem; }

.about-me__user-info__objective {
  font-size: .8rem;
  text-align: justify; }

.about-me__avatar {
  -webkit-flex: 0 0 30%;
  flex: 0 0 30%;
  text-transform: capitalize;
  font-size: 1.2rem; }

.about-me__contact {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 0 0 35%;
  flex: 0 0 35%;
  -webkit-flex-direction: column;
  flex-direction: column; }

.about-me__contact__info {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
  justify-content: flex-end;
  -webkit-align-items: center;
  align-items: center;
  padding: 2px 0; }

.about-me__contact__info svg {
  margin-left: 10px; }

.about-me__contact__info::after {
  font-family: "IcoFont";
  width: 24px;
  height: 24px;
  margin-left: 5px; }

.about-me__contact__info--email::after {
  content: '\EEA6'; }

.about-me__contact__info--linkedin::after {
  content: '\ED4D'; }

.about-me__contact__info--address::after {
  content: '\ED42'; }

.about-me__contact__info--mobile::after {
  content: '\EC54'; }

.about-me__contact__info--skype::after {
  content: '\ED67'; }
